$h1-sz: 4.8rem
$h2-sz: 3.8rem
$h3-sz: 3.0rem
$h4-sz: 2.4rem
$h5-sz: 2.0rem
$h6-sz: 1.6rem

$h-line: 1.2

h1, h2, h3, h4, h5, h6
	font-weight: 200
	letter-spacing: -0.04em
	line-height: $h-line

	span
		font-weight: 400
		transition: .4s
		&:hover
			font-weight: 710

h4, h5, h6
	text-transform: uppercase

$h-sizes: $h1-sz, $h2-sz, $h3-sz, $h4-sz, $h5-sz, $h6-sz

@for $i from 1 through length($h-sizes)
	h#{$i}
		font-size: nth($h-sizes, $i)