*
	padding: 0
	margin: 0
	box-sizing: border-box

*, *::before, *::after
	box-sizing: border-box
a
	color: inherit
	text-decoration: none

html
	scroll-behavior: smooth
img
	-webkit-user-select: none
	-khtml-user-select: none
	-moz-user-select: none
	-o-user-select: none
	user-select: none

	-webkit-user-drag: none
	-khtml-user-drag: none
	-moz-user-drag: none
	-o-user-drag: none
	user-drag: none

::selector
	background: transparent

:focus
	border: none
	outline: none
strong
	font-weight: 600


input
	font-size: 1rem
	padding: .5rem
	font-weight: 200
