// body, html, .page-wrap

html
	font-size: 20px

body
	font: 200 1rem / 1.5 Manrope, sans-serif

footer
	margin-top: auto


.page
	&-wrap
		background: rgba(0, 0, 0, 0.05)
		width: 100%
		// -webkit-scrollbar-width: 0
		// scrollbar-width: none
		// scrollbar-width: 0
		// max-height: 100vh
		// overflow: hidden

	&-content
		flex-direction: column
		display: flex
		border: rgba(0,0,0,.05) 3px dashed
		min-height: 100vh
		min-height: 1600px


	&-top-btn
		background: grey
		border-radius: 100%
		bottom: 40px
		height: 50px
		position: absolute
		position: fixed
		z-index: 100000
		right: 40px
		width: 50px

		span
			text-align: center
			margin: auto
			width: 20px
			height: 30px
			+cae

.main-cont
	max-width: $cont + px
	+hp($cont_inset)
	+ma
	width: 100%


