$status_list: dont_install, like, must_have, bad, review, paid

%hidden
	display: none

@for $i from 1 through length($status_list)
	@for $k from 1 through length($status_list)
		$elem: nth($status_list, $i)
		@if $elem != nth($status_list, $k)
			input[id='#{$elem}']:checked ~
				& .#{nth($status_list, $k)}
					@extend %hidden

.input-frame
	width: 100%
	margin: 10px $pl_h-gutter 40px

#search-input
	width: 100%
	background: white
	border-radius: 4px

.tabs-bottom
	width: 100%
	background: orange
	height: 1px
	+vm(20px)
	+hm($pl_h-gutter)

[name="tab-filter"]
	display: none

[name="tab-filter"] + label
	// display: inline-block.
	background: grey
	border-radius: 4px
	color: #fff
	cursor: pointer
	font-size: .5rem
	font-weight: 800
	letter-spacing: 0.2em
	padding: 12px 20px
	text-transform: uppercase
	transition: 0.3s
	&:first-of-type
		margin-left: $pl_h-gutter
	&:not(:first-of-type)
		margin-left: 10px
	&:hover
		background: #393939
[name="tab-filter"]:checked + label
	background: orange



.plugin
	&-list
		overflow: hidden
		display: flex
		flex-wrap: wrap
		margin-bottom: -$pl_bottom
		+hm(-2*$pl_h-gutter)
		+ot
		label
			align-self: center
	&-card
		width: 20%
		padding: 0 $pl_h-gutter
		margin-bottom: $pl_bottom
		&:nth-child
			color: red !important


	&-link
		width: 100%
		overflow: hidden

	&-content
		border-radius: $pl_radius
		overflow: hidden
		position: relative
		width: 100%
		transition: .3s
		color: transparentize(#393939,.3)
		box-shadow: 0 10px 12px -5px rgba(0,0,0,.05)



		&:hover
			color: #393939
			box-shadow: 0 10px 12px -5px rgba(0,0,0,.2)
		p
			position: absolute
			padding: 6px 10px 3px
			letter-spacing: 0.08em
			font-size: 10px
			font-weight: 800
			text-transform: uppercase
			// top: 0
			// left: 0
			z-index: 10
			background: transparentize(orange, .3)

			display: none

	&-preview
		// overflow: hidden
		position: relative
		padding-top: 56.25%
		width: 100%

		z-index: 1
		width: 100%
		height: 100%
		img
			position: absolute
			top: 0
			left: 0
			// border-radius: $pl_radius + 0.1
			object-fit: cover
			width: 100%
			height: 100%
	&-info
		background: white
		z-index: 2
		padding: 6px 10px 8px
		font-size: 14px
		strong
			padding: 0
			margin: 0
			text-overflow: ellipsis
			display: -webkit-box
			-webkit-line-clamp: 1
			-webkit-box-orient: vertical
			overflow: hidden