=hp($a:0)
	padding-left: #{$a} + px
	padding-right: #{$a} + px

=vp($a:0)
	padding-top: $a
	padding-bottom: $a

=hm($a:0)
	margin-left: $a
	margin-right: $a
%cae
	position: absolute
	top: 0
	bottom: 0
	right: 0
	left: 0

=cae
	@extend %cae

%ma
	margin-left: auto
	margin-right: auto
=ma
	@extend %ma

=vm($a:0)
	margin-top: $a
	margin-bottom: $a


=br($a: 1, $b: red)
	border: #{$a}px solid #{$b}